import { encodeDefxCallName, BIG_ZERO, OptionType, OptionCoin } from '@/common/util'

const oinitOptionValue = {
  lockupPeriodForHedgedTranches: BIG_ZERO, // 
  lockupPeriodForUnhedgedTranches: BIG_ZERO, // 
  totalBalance: BIG_ZERO, //  =  + 
  hedgedShare: BIG_ZERO, //  =  + 
  unhedgedShare: BIG_ZERO, // 
  hedgedBalance: BIG_ZERO, // 
  unhedgedBalance: BIG_ZERO, // 
  INITIAL_RATE: BIG_ZERO, // 
  maxUtilizationRate: BIG_ZERO, // 
  lockedAmount: BIG_ZERO, // 
  collateralizationRatio: BIG_ZERO, /// 
  allowance: BIG_ZERO,
  loaded: false
}

export default {
  namespaced: true,
  state: {
    options: {
      up_CAKE: {
        ...oinitOptionValue,
        key: encodeDefxCallName(OptionType.UP, OptionCoin.CAKE),
        coin: OptionCoin.CAKE,
        type: OptionType.UP,
      },
      down_CAKE: {
        ...oinitOptionValue,
        key: encodeDefxCallName(OptionType.DOWN, OptionCoin.CAKE),
        coin: OptionCoin.CAKE,
        type: OptionType.DOWN,
      },
      up_ETH: {
        ...oinitOptionValue,
        key: encodeDefxCallName(OptionType.UP, OptionCoin.ETH),
        coin: OptionCoin.ETH,
        type: OptionType.UP,
      },
      down_ETH: {
        ...oinitOptionValue,
        key: encodeDefxCallName(OptionType.DOWN, OptionCoin.ETH),
        coin: OptionCoin.ETH,
        type: OptionType.DOWN,
      },
    },
    dataInfo: {
      allowance: BIG_ZERO,
      loaded: false
    }
  },
  mutations: {
    setOption(state, {key, info}) {
      let s_info = state.options[key] || {};
      state.options[key] = {
        ...s_info,
        ...info,
        loaded: true,
      };
    },
    setDataInfo(state, data) {
      state.dataInfo = {
        ...state.dataInfo,
        ...data
      };
    },
  },
  actions: {
    setOption({commit}, params) {
      commit('setOption', params);
    },
    setDataInfo({commit}, params) {
      commit('setDataInfo', params);
    },
  },
}